import React, {Component} from 'react';
// import {Link} from 'react-router-dom'
import Breadcrumbs from '../components/breadcrumbs';
import styles from '../scss/contact.module.scss';
import ContactBg from '../assets/images/contact_bg.jpg';

class Contact extends Component {
  constructor(props){
    super(props)
    this.state={
      pagename:'',
      queation:'',
      name:'',
      phone:'',
      tel:'',
      mail:'',
      memo:''
    }
  }
  componentDidMount(){
    window.scrollTo(0,0)
  }
  SubmitData = (e) =>{
    e.preventDefault();
    console.log(this.state)
    alert(this.state.mail)
  }
  changequeation = (e) =>{
    this.setState({
      queation:e.target.value
    })
  }
  changeInput = (e) =>{
    this.setState({
      [e.target.name]:e.target.value
    })
  }
  render(){
    const {pagename,queation} = this.state
    return(
      <div> {/**/}
        <div className={styles.contactpage}>
          <img src={ContactBg} alt="background" />
        </div>
        <Breadcrumbs data={pagename}/>
        <div className={styles.contact_content}>
          <div className={styles.form_bgcolor}>
            <h2></h2>
            <h4></h4>
            <form onSubmit={this.SubmitData}>
              <div>
                <select value={queation} className={styles.tdwidth} onChange={this.changequeation}>
                  <option value="" disabled></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div><input type="text" name="name" maxLength="5" className={styles.tdwidth} placeholder=" " onChange={this.changeInput}/></div>
              <div><input type="text" name="phone"  className="tdwidth" placeholder=" "  onChange={this.changeInput}/></div>
              <div><input type="text" name="tel" maxLength="10" className={styles.tdwidth} placeholder=" "  onChange={this.changeInput}/></div>
              <div><input type="email" name="mail" className={styles.tdwidth} placeholder=""  onChange={this.changeInput}/></div>
              <div><textarea name="memo" placeholder=" " className={styles.font_size}  onChange={this.changeInput}/></div>
              <input type="submit" value="" className={styles.submitbtn}/>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

export default Contact;
